<template>
  <div class="main">
    <modal
      v-if="isModal"
      v-bind:modalMessage="modalMessage"
      @accept="renderHome"
      @close="renderHome"
    ></modal>
    <template v-if="home">
      <home-header @home="renderHome"></home-header>
      <home-page @register="startTest" @home="renderHome"></home-page>
    </template>
    <template v-else>
      <course-header @home="renderHome" @timeOver="changeTimeOver"></course-header>
      <course-page
        @home="renderHome"
        v-bind:timeLimit="timeLimit"
        v-bind:menu="menu"
      ></course-page>
    </template>
    <side-bar v-bind:homePage="home" @changeMenu="changeMenu"></side-bar>
  </div>
</template>
 
<script>
import CoursePage from "./components/CoursePage.vue";
import HomePage from "./components/HomePage.vue";
import SideBar from "./components/common/SideBar.vue";
import Modal from "./components/common/Modal.vue";
import HomeHeader from "./components/common/HomeHeader.vue";
import CourseHeader from "./components/common/CourseHeader.vue";
export default {
  data() {
    return {
      //  
      //   
      timeLimit: 20,
      //  
      home: true,
      //    
      menu: "noticeMenu",
      //   
      isModal: false,
      modalMessage: "    !",
    };
  },
  methods: {
    //        
    changeMenu(menu) {
      if (this.home == true) return;
      this.menu = menu;
    },
    renderHome() {
      this.isModal = false;
      this.timeOver = false;
      this.timeLimit = 0;
      this.home = true;
      location.reload();
    },
    startTest() {
      const timmer = setInterval(() => {
        this.timeLimit--;
      }, 1000);
      if (this.timeLimit == 0) {
        clearInterval(timmer);
        //   
        this.timeLimit = 0;
      }
      this.home = false;
    },
    changeTimeOver() {
      this.isModal = true;
    },
  },
  components: {
    "course-page": CoursePage,
    "home-page": HomePage,
    SideBar,
    Modal,
    HomeHeader,
    CourseHeader,
  },
};
</script>

