<script>

export default {
  props: ["isSearch"],
  data() {
    return {
      loading: true,
      contents: [],
      courseNum :''
    };
  },
  created() {
    setTimeout(() => {
      this.loading = false;
      this.contents = this.range(10);
    }, 1500);
  },
  methods: {
     range: (l) => Array.apply(null, { length: l }).map((_,index)=> index+1),
    searchCourse() {
        this.contents = [] 
        this.loading = true;
        setTimeout(() => {
          this.loading = false; 
          this.contents = this.courseNum.trim() === '' ? this.range(10) : this.range(1);
          this.$emit('searchNum',this.contents.length)
          return; 
        },3100);
    },
    keyPrevent(e){
      alert('        .')
      e.returnValue = false;

    },
     courseApply(num){
        setTimeout(()=>{
            this.$emit('apply',num); 
        },1500)
    }
  },
};
</script>

<style>
.pnl-search {
    margin-top: 15px;
    position: relative;
    margin-bottom: 15px;
    padding: 11px 20px;
    border-radius: 6px;
    border: 1px solid #e5e5e5;
}
.pnl-search table {
    width: calc(100% - 100px);
    max-width: 1300px;
    table-layout: fixed;
    border-spacing: 0;
    position: static;
    display: table;
    border-collapse: separate;
    text-indent: initial;
    border-color: grey;
}
.pnl-search td+th {
    padding-left: 25px;
}
.pnl-search th {
    padding: 0 0 2px;
    font-size: 12px;
    line-height: 1.1;
    word-spacing: -.5px;
    text-align: left;
    vertical-align: middle;
}
.pnl-search tbody {
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;
}
.pnl-search tr {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}
.pnl-search table th {
    position: relative;
    padding: 7px 3px 0 0;
    font-weight: 700;
    color: #4d4d4d;
    text-align: right;
    vertical-align: top;
}
.pnl-search table td {
    padding: 0 0 5px 5px;
}
.pnl-search select {
    outline: none;
    appearance: none;
    width: 100%;
    height: 34px;
    font-weight: 400;
    font-size: 13px;
    line-height: 34px;
    letter-spacing: -.5px;
    border: 1px solid #b9b9b9;
    border-radius: 0;
    box-sizing: border-box;
    border-color: #ccc;
    padding: 0 26px 2px 5px;
    background-repeat: no-repeat;
    background-position: right 8px top 50%;
    line-height: 28px;
}
.pnl-search input{
    width: 100%;
    height: 34px;
    font-weight: 400;
    font-size: 13px;
    line-height: 34px;
    letter-spacing: -.5px;
    border: 1px solid #b9b9b9;
    border-radius: 0;
    -moz-appearance: none;
    appearance: none;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-color: #ccc;
    outline: none;
    padding: 0 5px;
}
.pnl-search button.btn-search {
    position:absolute;
    top: 12px;
    right: 20px;
    background-color: #444;
    border-color: #444;
    color: #fff;
    background-repeat: no-repeat;
    background-position: 12px 8px;
    padding-left: 32px;
    box-sizing: border-box;
    width: 70px;
    height: 34px;
    line-height: 34px;
    font-size: 13px;
    font-weight: 400;
    cursor: pointer;
    border-radius: 3px;
}
.loading {
  width: 100%;
  height: 400px;
  background-color: white;
  position: relative;
}
.loading .loading-bar {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%);
  width: 83px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background-color: #444;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  border-radius: 3px;
}

</style>