  components: {
    "wish-title": WishTitle,
  },
  created() {
    const arr = Array.apply(null, { length: 10 }).map((_, index) => index + 1);
    arr.forEach((num) => {
      const obj = {};
      obj.num = num;
      num < 8 ? (obj.division = "") : (obj.division = "");
      this.contents.push(obj);
    });
  },
};
