<template>
  <div class="wish-content">
    <div class="itb-wrapper">
      <div class="itb wish-itms">
        <div class="itm">
          <a class="emt"></a>
          <a class="itm-apply"></a>
          <a class="itm-clf"></a>
          <a class="itm-snum">-</a>
          <a class="itm-cname"></a>
          <a class="itm-grd"></a>
          <a class="itm-znum"></a>
          <a class="itm-zanum"></a>
          <a class="itm-stime"></a>
          <a class="itm-gname"></a>
          <a class="itm-hakzum"></a>
          <a class="itm-time">/</a>
          <a class="itm-isgub"></a>
          <a style="flex:1;"></a>
        </div>
        <div
          class="itm"
          v-for="content in contents"
          v-bind:class="{
            'spare-itm': content.num >= 8 && !content.registered,
            'selected-itm': content.registered,
          }"
          v-bind:key="content.num"
        >
          <a class="itm-num">{{ content.num }}</a>
          <a class="itm-apply">
            <button
              v-if="!content.registered"
              @click="() => courseApply(content.num)"
              class="apply-btn"
            >
              
            </button>
            <span v-else style="color: red"></span>
          </a>
          <a class="itm-clf">{{ content.division }}</a>
          <a class="itm-snum">ADD123-01</a>
          <a style="text-align:left;" class="itm-cname">{{ content.name }}</a>
          <a class="itm-grd">0</a>
          <a class="itm-znum">30</a>
          <a class="itm-zanum">10</a>
          <a class="itm-stime"></a>
          <a class="itm-gname">{{ content.num }}</a>
          <a class="itm-hakzum">3</a>
          <a class="itm-time"> 10:30-13:00 ()</a>
          <a class="itm-isgub">04</a>
          <a style="flex:1;"></a>
        </div>
      </div>
    </div>
    <div class="itm-notice">
      <a
        ><i
          style="margin: 0 5px 0 15px; color: #39abd4; font-size: 20px"
          class="fas fa-exclamation-circle"
        >
        </i
        >        
        , <em>''</em>  <em>''</em>  
           .</a
      >
    </div>
  </div>
</template>

<script>
export default {
  props: ["isSearch", "courseFull", "savedCourses","refresh"],
  data() {
    return {
      loading: true,
      contents: [],
      courseNum: "",
      rigisterPause: false,
    };
  },
  created() {
    setTimeout(() => {
      if (this.savedCourses !== null) {
        this.contents = this.savedCourses;
        return;
      }
      this.makeContents();
    }, 1500);
  },
  methods: {
    range: (l) => Array.apply(null, { length: l }).map((_, index) => index + 1),
    makeContents() {
      const arr = this.range(10);
      this.contents = arr.map((num) => {
        const obj = {};
        obj.num = num;
        obj.division = num <= 7 ? "" : "";
        obj.name = num !== 10 ? "" : "  ";
        obj.registered = false;
        return obj;
      });
    },
    courseApply(num) {
      if (this.rigisterPause) return;
      this.rigisterPause = true;
      new Promise((resolve) => {
        setTimeout(() => {
          this.$emit("apply", num);
          //       
          if (this.courseFull || num === 10) {
            this.rigisterPause = false;
            return;
          }
          resolve();
        }, 1500);
      }).then(() => {
        this.doneContents(num);
        this.$emit("save", this.contents);
        this.rigisterPause = false;
      });
    },
    doneContents(num) {
      this.contents.filter((el, index) => {
        if (index + 1 === num) {
          el.registered = true;
        }
        return el;
      });
    },
        
  },
   watch: {
      refresh(){
        this.$emit('refresh','end') 
        this.contents=[];
        setTimeout(() => {
          this.makeContents();
      }, 500);
      }
   }
  };
</script>

